// API Route: /api/v1/chatbot/subscription
// Handles chatbot customization subscription management

import { NextRequest, NextResponse } from 'next/server';
import { supabaseAdmin } from '@/lib/supabase/server';

// GET: Fetch subscription details for a sub-account
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url);
    const subAccountId = searchParams.get('subAccountId');

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'Sub-account ID is required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Fetch subscription status view
    const { data: subscription, error: subscriptionError } = await supabase
      .from('ChatbotSubscriptionStatus')
      .select('*')
      .eq('subAccountId', subAccountId)
      .single();

    if (subscriptionError && subscriptionError.code !== 'PGRST116') {
      throw subscriptionError;
    }

    // Fetch available plans
    const { data: plans, error: plansError } = await supabase
      .from('ChatbotSubscriptionPlan')
      .select('*')
      .eq('active', true)
      .order('order', { ascending: true });

    if (plansError) throw plansError;

    return NextResponse.json({
      success: true,
      data: {
        subscription: subscription || null,
        availablePlans: plans || [],
      },
    });
  } catch (error: any) {
    console.error('Error fetching subscription:', error);
    return NextResponse.json(
      {
        success: false,
        error: error.message || 'Failed to fetch subscription details',
      },
      { status: 500 }
    );
  }
}

// POST: Create a new subscription
export async function POST(request: NextRequest) {
  try {
    const body = await request.json();
    const { subAccountId, planId, billingCycle = 'monthly' } = body;

    if (!subAccountId || !planId) {
      return NextResponse.json(
        { error: 'Sub-account ID and plan ID are required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Get plan details
    const { data: plan, error: planError } = await supabase
      .from('ChatbotSubscriptionPlan')
      .select('*')
      .eq('id', planId)
      .single();

    if (planError || !plan) {
      return NextResponse.json(
        { error: 'Invalid plan ID' },
        { status: 400 }
      );
    }

    // Check for existing active subscription
    const { data: existingSubscription } = await supabase
      .from('ChatbotSubscription')
      .select('*')
      .eq('subAccountId', subAccountId)
      .eq('status', 'active')
      .single();

    if (existingSubscription) {
      return NextResponse.json(
        {
          error: 'An active subscription already exists. Please cancel or upgrade instead.',
          existingSubscription,
        },
        { status: 409 }
      );
    }

    // Calculate amount based on billing cycle
    const amount = billingCycle === 'annual' ? plan.annualPrice : plan.monthlyPrice;

    // Calculate next billing date
    const startDate = new Date();
    const nextBillingDate = new Date(startDate);
    if (billingCycle === 'monthly') {
      nextBillingDate.setMonth(nextBillingDate.getMonth() + 1);
    } else {
      nextBillingDate.setFullYear(nextBillingDate.getFullYear() + 1);
    }

    // Create subscription
    const newSubscription = {
      id: `sub_${Date.now()}_${Math.random().toString(36).substring(7)}`,
      subAccountId,
      planId,
      status: 'active',
      billingCycle,
      startDate: startDate.toISOString(),
      nextBillingDate: nextBillingDate.toISOString(),
      amount,
      currency: plan.currency || 'USD',
    };

    const { data: subscription, error: subscriptionError } = await supabase
      .from('ChatbotSubscription')
      .insert([newSubscription])
      .select()
      .single();

    if (subscriptionError) throw subscriptionError;

    // Update customization table with subscription details
    await supabase
      .from('ChatbotCustomization')
      .update({
        subscriptionActive: true,
        subscriptionTier: plan.name,
        subscriptionStartDate: startDate.toISOString(),
        monthlyPrice: plan.monthlyPrice,
        annualPrice: plan.annualPrice,
      })
      .eq('subAccountId', subAccountId);

    return NextResponse.json({
      success: true,
      data: subscription,
      message: `Successfully subscribed to ${plan.displayName} plan`,
      nextBillingDate: nextBillingDate.toISOString(),
    });
  } catch (error: any) {
    console.error('Error creating subscription:', error);
    return NextResponse.json(
      {
        success: false,
        error: error.message || 'Failed to create subscription',
      },
      { status: 500 }
    );
  }
}

// PUT: Update subscription (upgrade/downgrade)
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json();
    const { subscriptionId, newPlanId, billingCycle } = body;

    if (!subscriptionId) {
      return NextResponse.json(
        { error: 'Subscription ID is required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Get current subscription
    const { data: currentSubscription, error: currentError } = await supabase
      .from('ChatbotSubscription')
      .select('*')
      .eq('id', subscriptionId)
      .single();

    if (currentError || !currentSubscription) {
      return NextResponse.json(
        { error: 'Subscription not found' },
        { status: 404 }
      );
    }

    // If changing plan
    if (newPlanId) {
      const { data: newPlan, error: planError } = await supabase
        .from('ChatbotSubscriptionPlan')
        .select('*')
        .eq('id', newPlanId)
        .single();

      if (planError || !newPlan) {
        return NextResponse.json(
          { error: 'Invalid plan ID' },
          { status: 400 }
        );
      }

      const newAmount = billingCycle === 'annual' ? newPlan.annualPrice : newPlan.monthlyPrice;

      // Update subscription
      const { data: updated, error: updateError } = await supabase
        .from('ChatbotSubscription')
        .update({
          planId: newPlanId,
          amount: newAmount,
          billingCycle: billingCycle || currentSubscription.billingCycle,
          updatedAt: new Date().toISOString(),
        })
        .eq('id', subscriptionId)
        .select()
        .single();

      if (updateError) throw updateError;

      // Update customization table
      await supabase
        .from('ChatbotCustomization')
        .update({
          subscriptionTier: newPlan.name,
          monthlyPrice: newPlan.monthlyPrice,
          annualPrice: newPlan.annualPrice,
        })
        .eq('subAccountId', currentSubscription.subAccountId);

      return NextResponse.json({
        success: true,
        data: updated,
        message: `Successfully upgraded to ${newPlan.displayName} plan`,
      });
    }

    // If just changing billing cycle
    if (billingCycle && billingCycle !== currentSubscription.billingCycle) {
      const { data: plan } = await supabase
        .from('ChatbotSubscriptionPlan')
        .select('*')
        .eq('id', currentSubscription.planId)
        .single();

      const newAmount = billingCycle === 'annual' ? plan.annualPrice : plan.monthlyPrice;

      const { data: updated, error: updateError } = await supabase
        .from('ChatbotSubscription')
        .update({
          billingCycle,
          amount: newAmount,
          updatedAt: new Date().toISOString(),
        })
        .eq('id', subscriptionId)
        .select()
        .single();

      if (updateError) throw updateError;

      return NextResponse.json({
        success: true,
        data: updated,
        message: `Billing cycle updated to ${billingCycle}`,
      });
    }

    return NextResponse.json(
      { error: 'No changes specified' },
      { status: 400 }
    );
  } catch (error: any) {
    console.error('Error updating subscription:', error);
    return NextResponse.json(
      {
        success: false,
        error: error.message || 'Failed to update subscription',
      },
      { status: 500 }
    );
  }
}

// DELETE: Cancel subscription
export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url);
    const subscriptionId = searchParams.get('subscriptionId');
    const reason = searchParams.get('reason');

    if (!subscriptionId) {
      return NextResponse.json(
        { error: 'Subscription ID is required' },
        { status: 400 }
      );
    }

    const supabase = supabaseAdmin;

    // Get subscription details
    const { data: subscription, error: fetchError } = await supabase
      .from('ChatbotSubscription')
      .select('*')
      .eq('id', subscriptionId)
      .single();

    if (fetchError || !subscription) {
      return NextResponse.json(
        { error: 'Subscription not found' },
        { status: 404 }
      );
    }

    // Cancel subscription (keep access until end of billing period)
    const { data: cancelled, error: cancelError } = await supabase
      .from('ChatbotSubscription')
      .update({
        status: 'cancelled',
        cancelledAt: new Date().toISOString(),
        cancelReason: reason || 'No reason provided',
        updatedAt: new Date().toISOString(),
      })
      .eq('id', subscriptionId)
      .select()
      .single();

    if (cancelError) throw cancelError;

    // Update customization table (will revert to free tier after end date)
    await supabase
      .from('ChatbotCustomization')
      .update({
        subscriptionActive: false,
        subscriptionEndDate: subscription.nextBillingDate,
      })
      .eq('subAccountId', subscription.subAccountId);

    return NextResponse.json({
      success: true,
      data: cancelled,
      message: `Subscription cancelled. Access will continue until ${new Date(subscription.nextBillingDate).toLocaleDateString()}`,
      accessUntil: subscription.nextBillingDate,
    });
  } catch (error: any) {
    console.error('Error cancelling subscription:', error);
    return NextResponse.json(
      {
        success: false,
        error: error.message || 'Failed to cancel subscription',
      },
      { status: 500 }
    );
  }
}
